import { PropsWithChildren } from "react"
import { Box, Skeleton } from "@rp/react/web/mui/core"

function WatchlistDataTableLoadingCell({
  isLoading,
  children,
}: PropsWithChildren<{ isLoading?: boolean }>) {
  if (isLoading)
    return (
      <Box width={"100%"} height="100%" display="flex" alignItems="center">
        <Skeleton width={"90%"} />
      </Box>
    )
  return children
}

export { WatchlistDataTableLoadingCell }
